*{
    margin:0;
    padding:0;
    
  }
  body{
    font-family: 'monserratt',sans-serif;
    color:black;
    
    opacity:0.9;
    z-index:1;
  }
  .form{
   display:flex;
  width:100%;
  padding:15px;
  appearance:none;
  border:none;
  outline:none;
   margin-top:25px;
  font-size:20px;
  background-color:transparent;
 
   
}


.form:hover{
  cursor:pointer;
  
 
}
.form:focus{
  border:none;
}
.form:active{
  border:none;
  
}
  .app{
    margin:0;
    padding:0;
    background-image: url('../assets/cold.jpg');
    background-size:cover;
    background-repeat: no-repeat;
    min-height: 100vh;
     
    
 
   
  }
   .heat{
    background-image: url('../assets/heat.jpg');
    background-size:cover;
    background-repeat: no-repeat;
    min-height: 100vh;
     
    
 
   
  }
  .container{
   margin-top:5px;
    text-align:center;
    align-items: center;
